<template>
  <div id="notFoundMain">
    <p style="font-weight:bold;font-size:26px;">Not Found</p>
    <p>对不起，请求路径 <span>{{path}}</span> 不存在，请检查路径是否正确！ <a @click="back" style="color:#41babc" title="点击返回前一页">返回前一页</a></p>
  </div>

</template>

<script>
export default {
  mounted(){
    
  },
  data(){
    return {
      path:location.hash.substring(1,location.hash.length)
    }
  },
  methods:{
    back(){
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="less" scoped>
 #notAuthMain{
   color:#000;
   padding: 20px;
   h1{
     font-size: 32px;
     margin-bottom: 25px;
   }
   p{
     font-size: 16px;
     span{
       color:#41babc;
       padding:0 3px;
     }
     a{
       text-decoration: underline;
     }
   }
 }
</style>




